[md-ripple] {
  overflow: hidden; }

[md-ripple].md-ripple-unbounded {
  overflow: visible; }

.md-ripple-background {
  background-color: rgba(0, 0, 0, 0.0588);
  opacity: 0;
  transition: opacity 300ms linear;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0; }

.md-ripple-unbounded .md-ripple-background {
  display: none; }

.md-ripple-background.md-ripple-active {
  opacity: 1; }

.md-ripple-focused .md-ripple-background {
  opacity: 1; }

.md-ripple-foreground {
  background-color: rgba(0, 0, 0, 0.0588);
  border-radius: 50%;
  pointer-events: none;
  opacity: 0.25;
  position: absolute;
  transition: opacity, transform 0ms cubic-bezier(0, 0, 0.2, 1); }

.md-ripple-foreground.md-ripple-fade-in {
  opacity: 1; }

.md-ripple-foreground.md-ripple-fade-out {
  opacity: 0; }

.md-visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  text-transform: none;
  width: 1px; }

.md-overlay-container {
  position: fixed;
  pointer-events: none;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1000; }

.md-overlay-pane {
  position: absolute;
  pointer-events: auto;
  box-sizing: border-box;
  z-index: 1000; }

.md-overlay-backdrop {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  pointer-events: auto;
  transition: opacity 400ms cubic-bezier(0.25, 0.8, 0.25, 1);
  opacity: 0; }

.md-overlay-backdrop.md-overlay-backdrop-showing {
  opacity: 0.48; }

.md-overlay-dark-backdrop {
  background: #212121; }

.md-overlay-transparent-backdrop {
  background: none; }

.md-ripple-focused .md-ripple-background {
  background-color: rgba(176, 190, 197, 0.1); }

.md-datepicker-trigger {
  color: rgba(255, 255, 255, 0.3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.12); }
  md-datepicker:focus .md-datepicker-trigger {
    color: #c2185b;
    border-bottom: 1px solid #c2185b; }
  .ng-invalid.ng-touched .md-datepicker-trigger {
    color: #f44336;
    border-bottom: 1px solid #f44336; }

.md-datepicker-arrow {
  color: rgba(255, 255, 255, 0.3); }
  md-datepicker:focus .md-datepicker-arrow {
    color: #c2185b; }
  .ng-invalid.ng-touched .md-datepicker-arrow {
    color: #f44336; }

.md-datepicker-content {
  background: #424242; }

.md-datepicker-value {
  color: white; }
